/* Services */
.Services_card {
    background-color: #fff;
    padding: 15px 35px;
    border-radius: 5px;
    position: relative;
}
.Services_card:before {
    right: 0px;
    content: '';
    position: absolute;
    height: 2%;
    width: 30%;
    background-color: #EE3565;
    top: 0px;
    transition: 0.5s ease-in;
}
.Services_card:after {
    right: 0px;
    content: '';
    position: absolute;
    height: 30%;
    width: 2%;
    background-color: #EE3565;
    top: 0px;
        transition: 0.5s ease-in;

}
.Services_card:hover:after  {
    height: 100%;
}
.Services_card:hover:before  {
    width: 100%;
}
.Services_card:hover .innerDiv:after  {
    height: 110%;
    overflow: hidden;
}
.Services_card:hover .innerDiv:before  {
    width: 138%;
    overflow: hidden;
}
.innerDiv {
    position: relative;
}

.innerDiv:before {
    right: 0px;
    content: '';
    position: absolute;
    height: 2%;
    width: 30%;
    background-color: #EE3565;
    bottom: -15px;
    left:-35px;
        transition: 0.5s ease-in;

}
.innerDiv:after {
    right: 0px;
    content: '';
    position: absolute;
    height: 30%;
    width: 2%;
    background-color: #EE3565;
     bottom: -15px;
    left:-35px;
        transition: 0.5s ease-in;

}
/* marquee */
@keyframes marqueeScroll {
0% {
    transform: translateX(-50%);
}
100% {
    transform: translateX(0%);
}
}

.MarqueeLine {
  overflow: hidden;
  white-space: nowrap;
  position: relative;
}

.MarqueeLine ul {
  display: flex;
  align-items: center;
  gap: 3rem; /* spacing between logos */
  width: max-content;
      animation: marquee-horizontal-alt 120s 
linear infinite;
 
}

.MarqueeLine li {
  list-style: none;
  flex: 0 0 auto;
}

