/* title  */
h2.card-title {
    position: relative;
    display: inline;
}

h2.card-title:before {
    position: absolute;
    content: '';
    height: 2px;
    width: 100%;
    background-color: #EE3565;
    bottom: -15px;
}

h2.card-title:after {
    position: absolute;
    content: '';
    height: 2px;
    width: 10%;
    background-color: #60C3AC;
    bottom: -15px;
    left:0px;
    transition: 0.5s ease-in;
}

h2.card-title:hover:after {
    width:100%;
