    /* Bubble btn  */
    .Bubble_btn {
    background-color: #fff;
    padding: 10px 43px;
    border-radius: 5px;
    font-size: 18px;
    font-weight: 600;
    transition: 0.5s 
ease-in-out;
    position: relative;
    overflow: hidden;
    z-index: 1;
    position: relative;
    overflow: hidden;
    z-index: 1;
    display: inline-block;
}
.Bubble_btn:hover span {
    transform: translateY(0) scale(2);
}
.Bubble_btn:hover span.hover_text {
transform:unset !important;
color:#fff;
}
.Bubble_btn span:nth-child(2) , .Bubble_btn span:nth-child(3) , .Bubble_btn span:nth-child(4) ,.Bubble_btn span:nth-child(5) {
    position: absolute;
    width: 25%;
    height: 100%;
    background-color: #EE3565;
    /* transform: translateX(400%); */
    left: calc((var(--n) - 1) * 25%);
    transition: 0.5s;
    transition-delay: calc((var(--n) - 1) * 0.1s);
    z-index: -1;
        bottom: -100px;
}
 .Bubble_btn span:nth-child(3),.Bubble_btn span:nth-child(5) {
      background-color: #EE3565;
 }
.Bubble_btn span:nth-child(2) {
    --n: 1;
}

.Bubble_btn span:nth-child(3) {
    --n: 2;
}

.Bubble_btn span:nth-child(4) {
    --n: 3;
}

.Bubble_btn span:nth-child(5) {
    --n: 4;
}
