/* Media query */
@media screen and (min-width: 320px) and (max-width: 992px) { 
    .navigation nav {
        display: none;
    }
    .mobileNavigation {
        display: flex !important;
    }
    .mobileNavigation {
        transition: 0.8s 
        ease-in;
        position: absolute;
        display: flex;
        flex-direction: column;
        background-color: #fff;
        height: 100vh;
        top: 0px;
        left: -100%;
        padding: 25px;
        width: 50%;
    }
    .mobileNavigation.active {
        left: 0px;
    }
    .Mobile_menu {
        display: block !important;
    }
    .flex-coloum {
        flex-direction: column;
    }
